import React from 'react'
import { useSelector } from 'react-redux';

const ConfirmSelectionModal = ({ selectedRentableItems, closeModal, openRegistrationModal }) => {

    const apartment = useSelector((state) => { return state.userSelection.apartment; });
    const floorplan = useSelector((state) => { return state.userSelection.floorPlan; });
    const leaseterm = useSelector((state) => { return state.userSelection.leaseTerm; });

    const { name, bedrooms, bathrooms, livingArea, livingAreaMeasure } = floorplan
    const { term, displayDate, price } = leaseterm
    const { unitNumber } = apartment
    return (
        <div className='modal display-block' id='staticBackdrop'>
            <div className='modal-dialog modal-md modal-dialog-centered'>
                <div class='modal-content'>
                    <div className='modal-header modal-header-with-bg '>
                        <p className='mx-3' style={{
                            color: 'white',
                            fontSize: 19,
                            fontWeight: "bold",
                            marginTop: 20
                        }}>Confirm Selections</p>
                    </div>

                    <div className="modal-body">
                        <p className="pb-3 fs-5">
                            If the following selections are correct click apply now.
                        </p>

                        <div class="m-auto p-2 mt-4" style={{
                            width: '340px',
                            }}>

                        <h4>Floorplan - <span>{name}</span></h4>
                        <table class="summary selectionsTable mt-0">
                            <tbody>
                                <tr>
                                    <td>Apartment</td>
                                    <td><h4>{unitNumber}</h4></td>
                                </tr>
                                <tr>
                                    <td>Bedrooms</td>
                                    <td><h4>{bedrooms}</h4></td>
                                </tr>
                                <tr>
                                    <td>Bathrooms</td>
                                    <td><h4>{bathrooms}</h4></td>
                                </tr>
                                <tr>
                                    <td>{livingAreaMeasure === 'sqMeters' ? 'Square Meters' : 'Square Feet'}</td>
                                    <td><h4>{livingArea}</h4></td>
                                </tr>
                            </tbody>
                        </table>
                        <hr></hr>
                        <h4 class="mt-4">Lease Terms</h4>
                        <table class="summary selectionsTable">
                            <tbody><tr>
                                <td>Term</td>
                                <td><h4>{term} Months</h4></td>
                            </tr>
                                <tr>
                                    <td>Rent</td>
                                    <td><h4>${price.toFixed(2)}</h4></td>
                                </tr>
                            </tbody>
                        </table>
                        <hr></hr>
                        <h4 class="mt-4">{selectedRentableItems.length === 0 ? '' : 'Rentable Items'} </h4>
                        <table class="summary selectionsTable">
                            <tbody>
                                {selectedRentableItems.map((data) => {
                                    return [
                                        <tr>
                                            <td>{data.name}</td>
                                            <td><h4>{`$${data.price.toFixed(2)} / month`}</h4></td>
                                        </tr>,
                                    ]
                                })}
                            </tbody>
                        </table>
                        </div>
                        <div class="row">
                            <div class="">
                                <div class="row">
                                    <div className="col-md-6">
                                        <button
                                            type="button"
                                            button="selections"
                                            className="secondary target"
                                            onClick={closeModal}
                                        >Cancel </button>
                                    </div>
                                    <div className="col-md-6">
                                        <button
                                            type="button"
                                            button="selections"
                                            onClick={openRegistrationModal}
                                        >Continue </button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default ConfirmSelectionModal
