import React from 'react';
import { useDispatch } from 'react-redux';
import { setScreen } from '../../redux/slices/configurationSlice';
import { setApartmentNumber } from '../../redux/slices/searchFilterSlice';
import { setLeaseTerm } from '../../redux/slices/userSelectionSlice';

const BackButton = ({ screen }) => {
  const dispatch = useDispatch();
  return <div className='col-xs-push-12'>
    <button className="target secondary"
      button="secondary"
      onClick={async (e) => {
        await dispatch(setApartmentNumber(''));
        await dispatch(setLeaseTerm({}));
        await dispatch(setScreen(screen));
      }}
    >
      Back
    </button>
  </div>;
};

export default BackButton;
