import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
  CustomSelect,
  CustomDatePicker,
} from '../components/Filters';
import {
  SELECT_TYPE_LABEL,
} from '../constants/Filters/SelectOptionValue';
import PricingDetailsCard from '../components/Cards/PricingDetailsCard';
import PricingCalendarButton from '../components/LeaseTerm/PricingCalendarButton';
import '../App.css';
import { setLeaseTerm } from '../redux/slices/userSelectionSlice';
import { setTerm, setMoveInDate } from '../redux/slices/searchFilterSlice';
import PricingCalendarModal from '../components/Modals/PricingCalendarModal';
import { format, isAfter, parse, parseISO, startOfDay } from 'date-fns';
import { loadBestUnitPricing, loadUnitPricing } from '../services/ApartmentQuery/apartmentQueries';
import Spinner from '../components/Common/Spinner';
import NoResultsFound from '../components/Common/NoResultsFound';
import BackButton from '../components/Common/BackButton';
import { Screens, SELECTION_HEADER } from '../constants/Screens/Screens';
import { setScreen } from '../redux/slices/configurationSlice';
import { setLeaseTermOptions } from '../redux/slices/searchResultSlice';
import { getLeaseTermOptions, isBetweenPricingDates, selectedMatchesTerm } from '../services/utils/pricingHelper';
import { TYPES } from '../constants/PricingMatrix/SelectionType';
import SelectionHeader from '../components/Common/SelectionHeader';

function Pricing() {
  const dispatch = useDispatch();
  const apartment = useSelector((state) => { return state.userSelection.apartment; });
  const floorPlan = useSelector((state) => { return state.userSelection.floorPlan; });
  const filters = useSelector((state) => { return state.searchFilter; });
  const selection = useSelector((state) => { return state.userSelection; });
  const mininmumDate = filters.leaseTermMinDate;

  const [showPricingCalendarModal, setShowPricingCalendarModal] = useState(false);
  const [pricingMoveInDate, setPricingMoveInDate] = useState(filters.moveInDate || format(new Date(), 'yyyy-MM-dd'));
  const [pricingTerm, setPricingTerm] = useState(filters.term || 12);
  const [pricingResults, setPricingResults] = useState([]);
  const [loading, setLoading] = useState(true);
  const [initialLoading, setInitialLoading] = useState(true);
  const [selectedTerm, setSelectedTerm] = useState(pricingTerm);
  const [bestValueTerm, setBestValueTerm] = useState({});
  const [greatValueTerm, setGreatValueTerm] = useState({});
  const [pricingTermOptions, setPricingTermOptions] = useState([]);
  const [bestValueLoaded, setBestValueLoaded] = useState(false);

  const handleSelectLeaseTerm = async (data, moveInDate) => {
    dispatch(setLeaseTerm(data));
    dispatch(setTerm(data.term));
    dispatch(setMoveInDate(format(parse(moveInDate, 'MM/dd/yyyy', new Date()), 'yyyy-MM-dd')));
    dispatch(setScreen(Screens.ITEMS));
  };

  const viewPricingCalendar = () => {
    setShowPricingCalendarModal(!showPricingCalendarModal);
  };

  const closePricingCalendar = () => {
    setShowPricingCalendarModal(!showPricingCalendarModal);
  };

  const savePricingCalendar = (selectedTerm, selectedDate) => {
    setPricingTerm(selectedTerm);
    setPricingMoveInDate(selectedDate);
    closePricingCalendar();
  };

  const getBestGreatPricing = async () => {
    try {
      const data = await loadBestUnitPricing(apartment.id);

      const date = startOfDay(parseISO(mininmumDate));
      if (data && data.length > 0) {
        const best = data[0];
        best.displayDate = best.startDate;
        if(isAfter(parseISO(best.endDate), date)) {
          if (isBetweenPricingDates(date, best)) {
            best.displayDate = format(date, 'yyyy-MM-dd\'T\'HH:mm:ss.SSS+0000');
          }
          setBestValueTerm(best);
        }

        const great = data[1];
        great.displayDate = best.startDate;
        if(isAfter(parseISO(great.endDate), date)) {
          if (isBetweenPricingDates(date, great)) {
            great.displayDate = format(date, 'yyyy-MM-dd\'T\'HH:mm:ss.SSS+0000'); 
          }
          setGreatValueTerm(great);
        }
      }

    } catch (error) {
      console.log(JSON.stringify(error));
    } finally {
      setBestValueLoaded(true);
    }
  };

  const getUnitPricing = async () => {

    const results = [];
    const moveDate = format(parseISO(pricingMoveInDate), 'yyyy-MM-dd\'T\'HH:mm:ss.SSS+0000'); // +0000 instead of XX getting actual timezone
    console.log(`Move-in Date: ${moveDate}`);
    const body = {
      'moveInDate': moveDate,
    };
    if (!initialLoading) {
      body.term = pricingTerm;
    }
    try {
      const { records } = await loadUnitPricing(apartment.id, body);
      console.log(records);

      if (records && records.length > 0) {
        if (initialLoading) {
          getBestGreatPricing();

          const filtered = [];
          const options = getLeaseTermOptions(records, pricingTerm);
          let filterTerm = pricingTerm;

          setPricingTermOptions(options);
          if (!(pricingTerm in options)) {
            filterTerm = options[Object.keys(options)[0]].value;
            setSelectedTerm(filterTerm);
          }
          records.map((priceRecord) => {
            if (priceRecord.term == filterTerm) {
              filtered.push(priceRecord);
            }
          });
          setPricingResults(filtered);
          setInitialLoading(false);
        } else {
          setSelectedTerm(pricingTerm);
          setPricingResults(records);
        }
      } else {
        setBestValueLoaded(true);
      }

    } catch (error) {
      console.log(JSON.stringify(error));
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    setLoading(true);
    // refresh browser handling
    if (Object.keys(selection.floorPlan).length === 0) {
      dispatch(setScreen(Screens.FLOORPLANS));
    }
    setLoading(true);
    const delayDebounceFn = setTimeout(() => {
      getUnitPricing();
      dispatch(setLeaseTermOptions(pricingTermOptions));
    }, 1000);
    return () => clearTimeout(delayDebounceFn);

  }, [pricingMoveInDate, pricingTerm]);

  return (
    <div id="LeaseTerm">
      <SelectionHeader title={SELECTION_HEADER.LEASETERMS} />
      <div className="row">
          <CustomDatePicker onChange={setPricingMoveInDate} value={pricingMoveInDate} mininmumDate={mininmumDate} />
          {!initialLoading
          ?
            [
              <CustomSelect label={SELECT_TYPE_LABEL.LEASETERM} options={pricingTermOptions} onChange={setPricingTerm} value={pricingTerm} includeAll={false} selectedValue={pricingTerm} />,
              <PricingCalendarButton viewPricingCalendar={viewPricingCalendar} />
            ]
          :
          ''
          }
        </div>
      <div className='row'>
        {!loading && bestValueLoaded
          ? (
            pricingResults.length > 0
              ? [
                pricingResults.map((leaseterm) => {
                  return <PricingDetailsCard
                    data={leaseterm}
                    moveInDate={format(parseISO(pricingMoveInDate), 'MM/dd/yyyy')}
                    select={handleSelectLeaseTerm}
                    cardLabel={
                      selectedMatchesTerm(bestValueTerm, pricingTerm, pricingMoveInDate)
                        ? TYPES.CURRENT_BEST : TYPES.CURRENT_SELECTION
                    }
                  >
                  </PricingDetailsCard>;
                }),
                !selectedMatchesTerm(bestValueTerm, pricingTerm, pricingMoveInDate) && Object.keys(bestValueTerm).length != 0
                  ? <PricingDetailsCard
                    data={bestValueTerm}
                    moveInDate={`${format(parseISO(bestValueTerm.displayDate), 'MM/dd/yyyy')}`}
                    select={handleSelectLeaseTerm}
                    cardLabel={TYPES.BEST_VALUE}
                  />
                  : '',
                !selectedMatchesTerm(bestValueTerm, pricingTerm, pricingMoveInDate) && !selectedMatchesTerm(greatValueTerm, pricingTerm, pricingMoveInDate) && Object.keys(greatValueTerm).length != 0
                  ? <PricingDetailsCard
                    data={greatValueTerm}
                    moveInDate={`${format(parseISO(greatValueTerm.displayDate), 'MM/dd/yyyy')}`}
                    select={handleSelectLeaseTerm}
                    cardLabel={TYPES.GREAT_VALUE}
                  />
                  : '',
              ]
              : <NoResultsFound label={Screens.LEASETERMS} />
          )
          : <Spinner />}
        {!loading && bestValueLoaded
          ? <BackButton screen={Screens.APARTMENTS} /> : ''
        }
      </div>
      {
        showPricingCalendarModal === true && (
          <PricingCalendarModal
            options={pricingTermOptions}
            selectedMoveInDate={pricingMoveInDate}
            selectedTerm={selectedTerm}
            closeModal={closePricingCalendar}
            saveModal={savePricingCalendar}
            bestValueTerm={bestValueTerm}
            minDate={mininmumDate}
          />
        )
      }

    </div>

  );
}

export default Pricing;
