import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useForm } from "react-hook-form";
import { AsYouType, isValidNumber } from 'libphonenumber-js';
import { yupResolver } from '@hookform/resolvers/yup'
import * as Yup from 'yup'
import PasswordStrengthBar from 'react-password-strength-bar';
import { submitRegistration } from '../../services/UserRegistrationQuery/userRegistrationQueries';
import { setScreen } from '../../redux/slices/configurationSlice';
import { Screens } from '../../constants/Screens/Screens';

const filterProps = {
    companyName: 'Artesia Big Creek'
}

const formatPhoneNumber = (value) => {
  if (!value) return '';
  value = value.toString();
  if (value.includes('(') && !value.includes(')')) {
      return value.replace('(', '');
  }
  return new AsYouType('CA').input(value);
};

const UserRegistrationModal = ({ closeModal, openModal, selectedRentableItems }) => {
  const moveInDate = useSelector((state) => { return state.searchFilter.moveInDate; });
  const pricingId = useSelector((state) => { return state.userSelection.leaseTerm.id; });
  const [password, setPassword] = useState('');
  const [headerError, setHeaderError] = useState('');
  const [registered, setRegistered] = useState(false);
  const [passwordShown, setPasswordShown] = useState(false);
  const [confirmShown, setConfirmShown] = useState(false);

  const togglePasswordVisibility = () => {
    setPasswordShown(passwordShown ? false : true);
  };

  const toggleConfirmVisibility = () => {
    setConfirmShown(confirmShown ? false : true);
  };

  const formSchema = Yup.object().shape({
    firstName: Yup.string()
      .required('This field is required'),
    lastName: Yup.string()
      .required('This field is required'),
    email: Yup.string()
      .required('This field is required')
      .matches(/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i, 'Invalid Email Format'),
    phone: Yup.string()
      .required('This field is required')
      .test(
        'validatePhone',
        "Invalid Phone Format",
        (value) => { isValidNumber(value, 'CA') || isValidNumber(value, 'US') }
      ),
      // .min(14, 'Invalid Phone Format'),
    password: Yup.string()
      .required('Password is required')
      .min(8, 'Password must be at 8 characters long')
      .matches(
        /^.*(?=.{8,})((?=.*[!@#$%^&*()\-_=+{};:,<.>]){1})(?=.*\d)((?=.*[a-z]){1})((?=.*[A-Z]){1}).*$/,
        "Password must contain at least 8 characters, one uppercase, one number and one special case character"
      ),
    confirm: Yup.string()
      .required('Confirm Password is required')
      .oneOf([Yup.ref('password')], 'Passwords does not match'),
    agree: Yup.boolean()
      .oneOf([true], 'You must agree with Terms of Use')
  })
  const formOptions = { resolver: yupResolver(formSchema) };
  const { register, handleSubmit, formState: { errors } } = useForm(formOptions);
  const dispatch = useDispatch();
  
  const onSubmit = async(data) => {
    setHeaderError("");
    const featureIds = [...selectedRentableItems.map(item => item.id)];
    let body = { ...data, moveInDate, pricingId, featureIds };
    if (body.agree) delete body.agree;
    if (body.confirm) delete body.confirm;
    console.log("Validation Success, submitting...", body);
    let url = '';
    try {
      url = await submitRegistration(body);
      console.log("URL response", url);
      window.open(url, '_self');
      setRegistered(true);
    } catch (error) {
      console.log(JSON.stringify(error));
      setHeaderError("Error has been encountered. Please try again.");
    } finally {
      
    }
  }

  return (
    <div className='modal display-block calendar' id='staticBackdrop'>
      <div className='modal-dialog modal-lg modal-dialog-centered' style={{maxWidth:'600px',}}>
        <div className='modal-content'>
          <div className='modal-header modal-header-with-bg '>
                        <p className='mx-3' style={{
                            color: 'white',
                            fontSize: 19,
                            fontWeight: "bold",
                            marginTop: 20
                        }}>Register Now</p>
                    </div>
          <div className='modal-body' style={{margin:'10px'}}>
            { registered ?
              <div className="row">
                <p>Registration has been processed. Thank you.</p>
                <button type='reset' className="target secondary"
                  style={{marginRight: '5px'}}
                  button="secondary"
                  onClick={async(e) => {
                    await dispatch(setScreen(Screens.FLOORPLANS));                   
                  }}
                >
                  Close
                </button>
              </div> 
              :
            <form onSubmit={handleSubmit(onSubmit)}>
              {headerError && <p style={{color:'red',fontSize:'12px'}}>{headerError}</p>}
              <div className="row">
                <p className="pb-3 fs-5">Register your account to start the application process.</p>
              </div>
              <div className="row">
                <div className='form-group col-6 col-sm-6 col-md'>
                  <label>{'First Name'}</label>
                  <input
                    type="text"
                    className='form-control'
                    {...register("firstName")}
                  />
                  {errors.firstName && <p style={{color:'red',fontSize:'12px'}}>{errors.firstName.message}</p>}
                </div>
                <div className='form-group col-6 col-sm-6 col-md'>
                  <label>{'Last Name'}</label>
                  <input
                    type="text"
                    className='form-control'
                    {...register("lastName")}
                  />
                  {errors.lastName && <p style={{color:'red',fontSize:'12px'}}>{errors.lastName.message}</p>}
                </div>
              </div>
              <div className="row">
                <div className='form-group col-6 col-sm-6 col-md'>
                  <label>{'Email'}</label>
                  <input
                    type="text"
                    className='form-control'
                    {...register("email")}
                  />
                  {errors.email && <p style={{color:'red',fontSize:'12px'}}>{errors.email.message}</p>}
                </div>
                <div className='form-group col-6 col-sm-6 col-md'>
                  <label>{'Phone Number'}</label>
                  <input 
                    type="text"
                    className='form-control'
                    maxLength={16}
                    {...register("phone", {
                        onChange: (e) => e.target.value = formatPhoneNumber(e.target.value)
                      }
                    )}
                  />
                  {errors.phone && <p style={{color:'red',fontSize:'12px'}}>{errors.phone.message}</p>}
                </div>
              </div>
              <div className="row">
                <div className='form-group col-6 col-sm-6 col-md'>
                  <label>{'Password'}</label>
                  <input
                    type={passwordShown ? "text" : "password"}
                    className='form-control icon_showpassword'
                    onMouseDown={togglePasswordVisibility}
                    onMouseUp={togglePasswordVisibility}
                    {...register("password")}
                  />
                  {errors.password && <p style={{color:'red', fontSize:'12px'}}>{errors.password.message}</p>}
                  <PasswordStrengthBar password={password} scoreWordStyle={{fontSize:'10px'}} onChangeScore={(score, feedback)=> {console.log(score,feedback)}} />
                </div>
                <div className='form-group col-6 col-sm-6 col-md'>
                  <label>{'Confirm Password'}</label>
                  <input
                    type={confirmShown ? "text" : "password"}
                    className='form-control icon_showpassword'
                    onMouseDown={toggleConfirmVisibility}
                    onMouseUp={toggleConfirmVisibility}
                    {...register("confirm")}
                  />
                  {errors.confirm && <p style={{color:'red',fontSize:'12px'}}>{errors.confirm.message}</p>}
                </div>
              </div>
              <div className="mt-2 mb-5" style={{ wordWrap: 'unset'}}>
                  <input type="checkbox" className='form-control' style={{position:'fixed'}} {...register("agree")} /> 
                  <span style={{marginLeft:'30px'}}>I agree with {filterProps.companyName} <a href="#">Terms of Use</a></span>
                  {errors.agree && <p style={{color:'red',fontSize:'12px'}}>{errors.agree.message}</p>}
              </div>
              <div className="row">
                <div className='col-xs-push-12' style={{ textAlign: 'right'}}>
                  <button type='reset' className="target secondary"
                    style={{marginRight: '5px'}}
                    button="secondary"
                    onClick={openModal}
                  >
                    Cancel
                  </button>
                  <button className="target primary" 
                    button="primary"
                    type="submit"
                  >
                    Register
                  </button>
                </div>
              </div>
            </form>
            }
          </div>
        </div>
      </div>
    </div>
  )
}

export default UserRegistrationModal;