import React, { useEffect, useState } from 'react';
import SelectionHeader from '../components/Common/SelectionHeader';
import { SELECTION_HEADER } from '../constants/Screens/Screens';
import RentableItemCard from '../components/Cards/RentableItemCard';
import { useDispatch, useSelector } from 'react-redux';
import { loadRentableItems } from '../services/ApartmentQuery/apartmentQueries';
import { setScreen } from '../redux/slices/configurationSlice';
import { Screens } from '../constants/Screens/Screens';
import NoResultsFound from '../components/Common/NoResultsFound';
import Spinner from '../components/Common/Spinner';
import BackButton from '../components/Common/BackButton';
import ConfirmSelectionModal from '../components/Modals/ConfirmSelectionModal';
import UserRegistrationModal from '../components/Modals/UserRegistrationModal';

function RentableItems() {
  const dispatch = useDispatch();

  const apartment = useSelector((state) => { return state.userSelection.apartment; });
  const floorPlan = useSelector((state) => { return state.userSelection.floorPlan; });
  const moveInDate = useSelector((state) => { return state.userSelection.moveInDate; });
  const leaseTerm = useSelector((state) => { return state.userSelection.leaseTerm; });
  const [selectedRentableItems, setSelectedRentableItems] = useState([]);
  const [displayUserRegistrationModal, setDisplayUserRegistrationModal] = useState(false);
  const [displayConfirmSelectionModal, setDisplayConfirmSelectionModal] = useState(false);
  const [loading, setLoading] = useState(true);
  const [rentableItemResults, setRentableItemResults] = useState([]);

  const getRentableItems = async () => {

    try {
      setLoading(true);

      let data = await loadRentableItems(apartment.id);

      setRentableItemResults(data);

      console.log('Rentable Items:', data);
    } catch (error) {
      console.log(JSON.stringify(error));
    } finally {
      setLoading(false);
    }
  };

  const closeConfirmSelectionModal = () => {
    setDisplayConfirmSelectionModal(false);
  };

  const closeUserRegistrationModal = () => {
    setDisplayUserRegistrationModal(false);
  };

  const openRegistrationModal = () => {
    setDisplayConfirmSelectionModal(false);
    setDisplayUserRegistrationModal(true);
  }

  const openConfirmSelectionModal = () => {
    setDisplayUserRegistrationModal(false);
    setDisplayConfirmSelectionModal(true);
  }

  useEffect(() => {
    // refresh browser handling
    if (Object.keys(floorPlan).length === 0) {
      dispatch(setScreen(Screens.FLOORPLANS));
    }

    const delayDebounceFn = setTimeout(() => {
      getRentableItems();
    }, 1000);
    return () => clearTimeout(delayDebounceFn);
  }, []);

  return (
    <div id="RentableItems">
      <SelectionHeader title={SELECTION_HEADER.ITEMS} />
      <div>
        {!loading ?
          rentableItemResults.length > 0 ? rentableItemResults.map((data, index) => {
            return <div key={index}>
              <RentableItemCard
                data={data}
                selectedRentableItems={selectedRentableItems}
                setSelectedRentableItems={setSelectedRentableItems}>
              </RentableItemCard>
            </div>;
          })
            : <NoResultsFound label={Screens.ITEMS} />
          : <Spinner />}
      </div>
      <div style={{
        clear: 'both',
        paddingLeft: '12px',
        paddingTop: '30px'
      }}>
        <div style={{
          display: 'inline-block',
          verticalAlign: 'middle'
        }}>
          {!loading
            ? <BackButton screen={Screens.LEASETERMS} />
            : ''
          }
        </div>
        <div style={{
          paddingLeft: '12px',
          display: 'inline-block',
          verticalAlign: 'middle'
        }}>
          {!loading
            ? <button className="target primary"
              button="primary"
              onClick={async (e) => {
                setDisplayConfirmSelectionModal(true);
                console.log(rentableItemResults);
              }}
            >
              Apply Now
            </button>
            : ''
          }
        </div>
        {
          displayConfirmSelectionModal == true && (
            <ConfirmSelectionModal closeModal={closeConfirmSelectionModal} selectedRentableItems={selectedRentableItems} openRegistrationModal={openRegistrationModal} />
          )
        }
        {
          displayUserRegistrationModal == true && (
            <UserRegistrationModal closeModal={closeUserRegistrationModal} openModal={openConfirmSelectionModal} selectedRentableItems={selectedRentableItems} />
          )
        }
      </div>

    </div>

  );
}

export default RentableItems;