import { endOfDay, isValid, isWithinInterval, parseISO, startOfDay } from 'date-fns';

export function getLeaseTermOptions(leaseTerms, pricingTerm) {
  const options = [];

  if (leaseTerms.length > 0) {
    leaseTerms.map((priceRecord) => {
      const recordTerm = priceRecord.term;
      options[recordTerm] = {
        label: `${recordTerm  } Month${  recordTerm > 1 ? 's' : ''}`,
        value: recordTerm,
        isSelected: recordTerm == pricingTerm,
      };
    });
  }
  return options;
}

export function selectedMatchesTerm(termToMatch, selectedTerm, selectedMoveInDate) {
  const startDate = startOfDay(parseISO(termToMatch.startDate));
  const endDate = endOfDay(parseISO(termToMatch.endDate));

  const dateRange = { start: startDate, end: endDate};
  if(termToMatch.term == selectedTerm && 
    isBetweenPricingDates(selectedMoveInDate, termToMatch)) {
    return true;
  }

  return false;
}

export function isBetweenPricingDates(selectedDate, unitPricing) {
  const startDate = startOfDay(parseISO(unitPricing.startDate));
  const endDate = endOfDay(parseISO(unitPricing.endDate));
  const date = isValid(selectedDate) ? selectedDate : parseISO(selectedDate);
  const dateRange = { start: startDate, end: endDate};

  if(isWithinInterval(date, dateRange)) {
    return true;
  }

  return false;
}
