import React from 'react';
import { useDispatch } from 'react-redux';
import { setScreen } from '../../redux/slices/configurationSlice';
import { setApartmentNumber, setLeaseTermMinDate, setMoveInDate } from '../../redux/slices/searchFilterSlice';
import { setApartment } from '../../redux/slices/userSelectionSlice';
import { AREA_TEXT, Screens } from '../../constants/Screens/Screens';

const ApartmentList = ({ apartments, filterProps }) => {
  const dispatch = useDispatch();
  return (
    <div id="PV_PageContent_Main">
      {
        apartments.map((apartment) =>
          <div key={apartment.id} className="layout_select_list_grouping app-container">
            <div className="content">
              <div className="content_row">
                <div className="content_column">
                  <div className="col-xs-push-1">Apartment</div>
                  <div className="col-xs-push-1 detail">{apartment.unitNumber}</div>
                </div>
                <div className="content_column">
                  <div className="col-xs-push-1">Area</div>
                  <div className="col-xs-push-1 detail">{apartment.floorPlan.livingArea} {AREA_TEXT[apartment.floorPlan.livingAreaMeasure]}</div>
                </div>
                <div className="content_column">
                  <div className="col-xs-push-1">Price per Month</div>
                  <div className="col-xs-push-1 detail">
                    { apartment.minPrice ? `$${apartment.minPrice}` : '' } - { apartment.maxPrice ? `$${apartment.maxPrice}*` : '' }
                  </div>
                </div>
                <div className="content_column"></div>
                <div className="content_column button">
                  { apartment.minPrice && apartment.maxPrice ?
                  <button className="gold target"
                    button="primary"
                    onClick={async(e) => {
                      //await dispatch(setApartments(apartments));
                      await dispatch(setMoveInDate(filterProps.moveInDate));
                      await dispatch(setLeaseTermMinDate(filterProps.moveInDate));
                      await dispatch(setApartmentNumber(filterProps.apartmentNumber));
                      await dispatch(setApartment(apartment));
                      await dispatch(setScreen(Screens.LEASETERMS));
                    }}>
                  Select
                  </button>
                  : 
                  <a href="tel:555-555-5555">
                  <button className="gold target" button="primary">
                    Contact Us
                  </button>
                  </a>
                  }
                </div>
              </div>
            </div>
          </div>
        )
      }
      <p className="pricing_note">*price subject to change.</p>
    </div>
  );
};

export default ApartmentList;
