import { store } from '../../redux/store';
import coreApi from '../../config/api';
import { Floorplans, Units } from '../../tests/DataSets/testData';

const getConfiguration = () => {
  return store.getState().configuration;
};

const apiData = {
  url: 'floor_plan/search',
};

export const loadFloorPlan = async (body) => {
  try {
    const configuration = await getConfiguration();
    coreApi.defaults.headers.common['x-pmc'] = configuration.pmc;

    if (configuration.demo) {
      return Floorplans;

    } else {
      const {data} = await coreApi.post(`${configuration.apiUrl}/${apiData.url}/${configuration.buildingId}?withMedia=true&page=${configuration.page}&recordsPerPage=${configuration.recordsPerPage}`, body);
      const {records} = data;
      return {records};
    }
  } catch (error) {
    const errorResponse = error.toJSON();
    throw new Error(errorResponse);

  }
};

export const loadFloorPlanApartment = async (body) => {
  try {
    const configuration = await getConfiguration();
    coreApi.defaults.headers.common['x-pmc'] = configuration.pmc;
    
    if (configuration.demo) {
      return Units;
    } else {
      const {data} = await coreApi.post(`${configuration.apiUrl}/unit/search/${configuration.buildingId}?withMedia=true&page=${configuration.page}&recordsPerPage=${configuration.recordsPerPage}`, body);
      const {records} = data;
      return {records};
    }
  } catch (error) {
    const errorResponse = error.toJSON();
    throw new Error(errorResponse);
  }
};
