import axios from 'axios';
import { store } from '../redux/store';

const getApiUrl = () => {
  return store.getState().configuration.apiUrl;
};

const coreApi = axios.create({
  baseURL: getApiUrl(),
  headers: {
    'Content-Type': 'application/json',
    Accept: 'application/json',
  },
});

coreApi.interceptors.response.use((response) => {
  return response;
}, (errorResponse) => {
  const error = errorResponse.toJSON();

  if (error && error.message === 'Network Error') {
    throw new Error(error);
  } else if (error && error.status && error.response.status === 401) {
    // handle 401
  } else if (error.status == 404) {
    console.log(error)
    // handle 404
  } else if (error && error.status && error.status === 500) {
    throw new Error(error);
  }
  throw new Error(error);
});

coreApi.interceptors.request.use((request) => {
  // add headers here
  return request;
});

export default coreApi;
