import React from 'react';

const CustomDatePicker = ({ label, onBlur, value, type  }) => {
  return (
    <div className='form-group col-6 col-sm-6 col-md'>
      <label>{label}</label>
      <input
        type={type ? type : 'text'}
        value={value}
        className='form-control'
        placeholder='Search apartment number'
        onChange={(event) => onBlur(event.target.value, type)}
      />
    </div>
  );
};

export default CustomDatePicker;
