import React from 'react';
import { useDispatch } from 'react-redux';
import { Constants } from '../../constants/FloorPlan/FloorPlan';
import { AREA_TEXT, Screens } from '../../constants/Screens/Screens';
import { setLeaseTermMinDate, setMoveInDate } from '../../redux/slices/searchFilterSlice';
import NoResultsFound from '../Common/NoResultsFound';
function ApartmentCards({ data, selectApartment }) {
  const dispatch = useDispatch();
  
  return (
    <>
      {data.length > 0 ? (<div>
      </div>) : <div><NoResultsFound label={Screens.APARTMENTS} /></div>}
      {data.map((item, i) => {
        return (
          <>
            <p>
              {item.floorPlan.name} | {item.floorPlan.bedrooms} Bedroom | {item.floorPlan.bathrooms}  Bathrooms | {item.floorPlan.livingArea} {AREA_TEXT[item.floorPlan.livingAreaMeasure]}
            </p>
            <div>
              <div className="layout_select_list_grouping app-container" style={{ marginBottom: 10 }}>
                <div className="content">
                  <div className="content_row">
                  
                    <div className="content_column">
                      <div className="col-xs-push-1">Apartment</div>
                      <div className="col-xs-push-1 detail">{item.unitNumber}</div>
                    </div>
                    <div className="content_column">
                      <div className="col-xs-push-1">Area</div>
                      <div className="col-xs-push-1 detail"> {item.floorPlan.livingArea} {AREA_TEXT[item.floorPlan.livingAreaMeasure]} </div>
                    </div>
                    <div className="content_column">
                      <div className="col-xs-push-1">Price per Month</div>
                      <div className="col-xs-push-1 detail">{item.minPrice ? `$${item.minPrice}` : ''} - {item.maxPrice ? `$${item.maxPrice}*` : ''}</div>
                    </div>
                    <div className="content_column"></div>
                    <div className="content_column button">
                      { item.minPrice && item.maxPrice ?
                        (
                          item.showFutureDate ? 
                          <div>
                            <div>
                              <button
                                className="gold"
                                onClick={() => {
                                  selectApartment(item);
                                  dispatch(setMoveInDate(item.formattedAvailableDate));
                                  dispatch(setLeaseTermMinDate(item.formattedAvailableDate))
                                }}>
                                Select
                              </button>
                            </div>
                            <div className="pt-3"
                              style={{
                                fontSize: '12px',
                              }}>
                              This apartment is available from {item.availableDate}. Please select to proceed.
                            </div> 
                          </div>
                          : 
                          <button
                            className="gold"
                            onClick={() => selectApartment(item)}>
                            Select
                          </button>
                        )
                      :
                      <div className="button">
                        <a href="tel:555-555-5555">
                        <button className="gold">
                          Contact Us
                        </button>
                        </a>
                      </div>
                      }
                    </div>
                  </div>
                </div>
              </div>
            </div>

          </>
        );
      })}
    </>
  );
}

export default ApartmentCards;
