import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import '../App.css';
import '../components/Cards/Apartment.css';
import {
  CustomSelect,
  CustomDatePicker,
  CustomRangeFilter,
} from '../components/Filters';
import {
  BEDROOMS_OPTIONS,
  BATHROOMS_OPTIONS,
  DENS_OPTIONS,
  AREA_RANGE_VAL,
  RENT_RANGE,
  SELECT_TYPE_LABEL,
} from '../constants/Filters/SelectOptionValue';
import { loadFloorPlan, loadFloorPlanApartment } from '../services/FloorplanQuery/floorPlanQueries';
import { setScreen } from '../redux/slices/configurationSlice';
import { setFloorPlan } from '../redux/slices/userSelectionSlice';
import { setApartmentNumber, setBathrooms, setBedrooms, setDen, setMoveInDate, setRentRange, setAreaRange } from '../redux/slices/searchFilterSlice';
import { setApartment } from '../redux/slices/userSelectionSlice';
import { FloorPlanCards } from '../components/Cards';
import FloorPlanImageModal from '../components/Modals/FloorPlanImageModal';
import ApartmentCards from '../components/Cards/ApartmentCards';
import Spinner from '../components/Common/Spinner';
import NoResultsFound from '../components/Common/NoResultsFound';
import { Screens } from '../constants/Screens/Screens';

import { addMonths, endOfDay, endOfMonth, format, isAfter, isBefore, parse, parseISO, startOfDay } from 'date-fns';
import { Constants } from '../constants/FloorPlan/FloorPlan';
import { attempt, filter } from 'lodash';

function FloorPlan() {
  const dispatch = useDispatch();
  const filters = useSelector((state) => { return state.searchFilter; });
  const getMoveInDate = () => {
    return filters.moveInDate || format(new Date(), 'yyyy-MM-dd');
  };
  const [selectedDate, setSelectedDate] = useState(getMoveInDate);
  const [selectedApartment, setSelectApartment] = useState('');
  const [showImageModal, setShowImageModal] = useState(false);
  const [selectedImage, setSelectedImage] = useState({});
  const [loading, setLoading] = useState(false);

  const initialFieldValues = {
    bedrooms: {
      condition: '',
      value: 0,
    },
    bathrooms: {
      condition: '',
      value: 0,
    },
    moveInDate: format(new Date(), 'yyyy-MM-dd'),
    minLivingArea: 0,
    maxLivingArea: 0,
    minPrice: 0,
    maxPrice: 0,
  };

  const [values, setValues] = useState(initialFieldValues);

  const handleOnchange = async (val, field) => {
    // OnChange Bathroom and Bedroom Filter
    if ((field === 'bedrooms' || field === 'bathrooms')) {
      let condition = 'eq';
      if (val === '4+' || val === '3+') {
        condition = 'greater_than_eq';
        val = val.replace('+', '');
      }
      setValues({
        ...values,
        [field]: {
          value: Number(val),
          condition,
        },
      });
      dispatch(field === 'bedrooms' ? setBedrooms(val) : dispatch(setBathrooms(val)));
    }

    // OnChange Den Filter
    if (field == 'dens') {
      if (val != '0' && val != '1') {
        delete values.dens;
        setValues({
          ...values
        });
      } else {
        setValues({
          ...values,
          [field]: val == 1,
        });
      }
      dispatch(setDen(val));
    }

    // OnChange RentRange Filter
    if (field === 'rent_range' && val) {
      const myArr = val.split('-');
      const min = myArr[0];
      const max = myArr[1];
      setValues({
        ...values,
        minPrice: Number(min),
        maxPrice: Number(max),
      });

      dispatch(setRentRange({ min, max }));
    }

    // OnChange Area Range
    if (field === 'area_range' && val) {
      const myArr = val.split('-');
      const min = myArr[0];
      const max = myArr[1];

      setValues({
        ...values,
        minLivingArea: parseFloat(min),
        maxLivingArea: parseFloat(max),
      });
      dispatch(setAreaRange({ min, max }));
    }

    // OnChange MoveInDate
    if (field == 'moveInDate' && val) {
      const moveDate = format(parseISO(val), 'yyyy-MM-dd\'T\'HH:mm:ss.SSS+0000');
      setValues({
        ...values,
        [field]: moveDate,
      });
      setSelectedDate(val);
      dispatch(setMoveInDate(val));
    }
  };

  const handleSearch = async (e) => {

    setSelectApartment(e.target.value);
    const val = e.target.value;
    const v = document.querySelectorAll('input[type=date], select');
    if (val.length > 0) {
      v.forEach((opt) => {
        opt.disabled = true;
      });
    } else {
      v.forEach((opt) => {
        opt.disabled = false;
      });
    }

    dispatch(setApartmentNumber(val));
    try {
      const endOfNextMonth = endOfDay(endOfMonth(addMonths(parseISO(getMoveInDate()), 1)));
      const { records } = await loadFloorPlanApartment({ unitNumber: e.target.value, moveInDate: format(endOfNextMonth, 'yyyy-MM-dd\'T\'HH:mm:ss.SSS+0000') });
      const filtered = [];

      for (const apt of records) {
        const parsed = parseISO(apt.availableFrom);
        apt.availableDate = format(parsed, 'MM/dd');
        apt.showFutureDate = false;
        apt.formattedAvailableDate = format(parsed, 'yyyy-MM-dd');

        if (apt.status == Constants.AVAILABLE.toLowerCase()) {
          filtered.push(apt);
        } else {
          if (isBefore(parsed, endOfNextMonth)) {
            if (isAfter(parsed, startOfDay(parseISO(selectedDate)))) {
              apt.showFutureDate = true;
            }
            filtered.push(apt);
          }
        }
      }
      setFloorPlanApartmentList(filtered);
      console.log(filtered);
    } catch (error) {
      console.log(JSON.stringify(error));
    }
    finally {
      setLoading(false);
    }
  };

  const [floorPlanList, setFloorPlanList] = useState([]);
  const [floorPlanListApartment, setFloorPlanApartmentList] = useState([]);

  useEffect(() => {

    setLoading(true);
    async function getFloorplan() {
      try {
        const { records } = await loadFloorPlan(values);
        setFloorPlanList(records);
      } catch (error) {
        console.log(JSON.stringify(error));
      } finally {
        setLoading(false);
      }
    }
    const delayDebounceFn = setTimeout(() => {
      getFloorplan();
    }, 1000);
    return () => clearTimeout(delayDebounceFn);
  }, [values, filters]);

  const viewImage = (
    bathrooms,
    bedrooms,
    name,
    livingArea,
    livingAreaMeasure,
    downloadUrl
  ) => {
    setSelectedImage({
      bathrooms,
      bedrooms,
      name,
      livingArea,
      livingAreaMeasure,
      downloadUrl,
    });
    setShowImageModal(!showImageModal);
  };

  const closeViewImage = () => {
    setShowImageModal(!showImageModal);
  };

  const handleSelectApartment = async (apartment) => {
    await dispatch(setApartment(apartment.unitNumber));
    await dispatch(setApartment(apartment));
    await dispatch(setFloorPlan(apartment.floorPlan));
    await dispatch(setScreen(Screens.LEASETERMS));
  };

  const handleSelectFloorPlan = async (data) => {
    await dispatch(setFloorPlan(data));
    await dispatch(setScreen(Screens.APARTMENTS));
  };

  return (
    <div>
      <div className='row'>
        <CustomDatePicker type="moveInDate" onChange={handleOnchange} value={selectedDate} />
        <CustomSelect
          label={SELECT_TYPE_LABEL.BEDROOMS}
          onChange={handleOnchange}
          options={BEDROOMS_OPTIONS}
          type={'bedrooms'}
        />
        <CustomSelect
          label={SELECT_TYPE_LABEL.BATHROOMS}
          onChange={handleOnchange}
          options={BATHROOMS_OPTIONS}
          type={'bathrooms'}
        />
      </div>
      <div className="row">
        <CustomSelect
          label={SELECT_TYPE_LABEL.DENS}
          onChange={handleOnchange}
          options={DENS_OPTIONS}
          type={'dens'}
        />

        <CustomRangeFilter
          label={SELECT_TYPE_LABEL.RENT_RANGE}
          options={RENT_RANGE}
          type="rent_range"
          onChange={handleOnchange}
          symbol="$"
        />

        <CustomRangeFilter
          label={SELECT_TYPE_LABEL.AREA_RANGE}
          options={AREA_RANGE_VAL}
          type="area_range"
          onChange={handleOnchange}
          symbol=""
        />

        <div className='form-group col-6 col-sm-6 col-md'>
          <label>Apartment Number</label>
          <input type='text' className='form-control' placeholder='Search apartment number' onChange={handleSearch} />
        </div>
      </div>

      <div className='row'>
        {loading ? <Spinner /> : selectedApartment.length > 0
          ? <ApartmentCards data={floorPlanListApartment} selectApartment={handleSelectApartment} selectedDate={selectedDate} />
          : floorPlanList.length > 0 ? (
            floorPlanList.map((item, i) => {
               if (item.availableUnits !== 0) {
                  return <FloorPlanCards data={item} viewImage={viewImage} available={handleSelectFloorPlan} key={i} />;
               } 
            })
          ) : (
            <div>
              <NoResultsFound label={Screens.FLOORPLANS} />
            </div>
          )}
      </div>

      {
        showImageModal == true && (
          <FloorPlanImageModal closeModal={closeViewImage} data={selectedImage} />
        )
      }
    </div >
  );
}
export default FloorPlan;
