import React from 'react';

const CustomSelect = ({ label, options, value, onChange, type, selectedValue = '', includeAll = true }) => {
  return (
    <div className="form-group col-6 col-sm-6 col-md">
      <label>{label}</label>
      <select id={type} onChange={(event) => onChange(event.target.value, type)} defaultValue={selectedValue} value={value}>
        {
          includeAll
            ? (<option value="" selected >All</option>)
            : ''
        }
        {options &&
          options.map((item, i) => {
            const { label, value } = item;
            return (
              <option key={i} value={`${value}`}>
                {label}
              </option>
            );
          })}
      </select>
    </div>
  );
};

export default CustomSelect;
