import { store } from '../../redux/store';
// import coreApi from '../../config/api';
import axios from 'axios';

const coreApi = axios.create({
    baseURL: 'https://main.k8s.devpv.us/lease-app/api',
    headers: {
      'Content-Type': 'application/json',
      Accept: 'application/json',
    },
});

const getConfiguration = () => {
  return store.getState().configuration;
};

const apiData = {
  url: 'lease_app/public/start',
};

export const submitRegistration = async (body) => {
  try {
    const configuration = await getConfiguration();
    coreApi.defaults.headers.common['x-pmc'] = configuration.pmc;
    const {data} = await coreApi.post(`${apiData.url}`, body);
    return data;
  } catch (error) {
    console.log(error.response.data);
    throw error;
  }
};