import { store } from '../../redux/store';
import coreApi  from '../../config/api';
import { RentableItems, UnitPricing, Units } from '../../tests/DataSets/testData';


const getConfiguration = () => {
  return store.getState().configuration;
};

const apiData = {
  urlUnits: 'unit/search',
  urlUnitPricing: 'unit_pricing/search',
  urlBestUnitPricing: 'unit_pricing/best',
  urlRentableItems: 'unit_features'
};

export const loadUnits = async (body) => {
  try {
    const configuration = getConfiguration();
    coreApi.defaults.headers.common['x-pmc'] = configuration.pmc;
    
    if (configuration.demo) {
      return Units;
    } else {
      const {data} = await coreApi.post(`${apiData.urlUnits}/${configuration.buildingId}?page=${configuration.page}&recordsPerPage=${configuration.recordsPerPage}`, body);
      const {records} = data;
      return {records};
    }
  } catch (error) {
    const errorResponse = error.toJSON();
    throw new Error(errorResponse);
  }
};

export const loadUnitPricing = async (unitId, body) => {
  try {
    const configuration = getConfiguration();
    coreApi.defaults.headers.common['x-pmc'] = configuration.pmc;
    
    if (configuration.demo) {
      return UnitPricing;
    } else {
      const {data} = await coreApi.post(`${apiData.urlUnitPricing}/${unitId}?page=${configuration.page}&recordsPerPage=${configuration.recordsPerPage}`, body);
      const {records} = data;
      return {records};
    }

  } catch (error) {
    const errorResponse = error.toJSON();
    throw new Error(errorResponse);
  }
};

export const loadBestUnitPricing = async (unitId) => {
  try {
    const configuration = getConfiguration();
    coreApi.defaults.headers.common['x-pmc'] = configuration.pmc;
    
    if (configuration.demo) {
      return [
        UnitPricing[3],
        UnitPricing[2]
      ];
    } else {
      const {data} = await coreApi.get(`${apiData.urlBestUnitPricing}/${unitId}?page=${configuration.page}&recordsPerPage=${configuration.recordsPerPage}`);
      return data;
    }
    
  } catch (error) {
    const errorResponse = error.toJSON();
    throw new Error(errorResponse);
  }
};

export const loadRentableItems = async (unitId) => {
  try {
    const configuration = getConfiguration();
    coreApi.defaults.headers.common['x-pmc'] = configuration.pmc;

    if (configuration.demo) {
      return RentableItems.records;
    } else {
      const {data} = await coreApi.get(`${apiData.urlRentableItems}/${unitId}?page=${configuration.page}&recordsPerPage=${configuration.recordsPerPage}`);
      return data;
    }
  } catch (error) {
    const errorResponse = error.toJSON();
    throw new Error(errorResponse);
  }
};